.navigation{
  padding: 20px 0;
  transition: .6s ease;
  @include tablet {
    padding: 15px;
  }
  &.nav-bg{
    padding: 5px 0;
    box-shadow: 0 0 25px rgba(7,51,84,.15);
  }
}

.nav-item{
  margin: 0 5px;
  position: relative;
  .nav-link{
    padding: 10px 20px !important;
    position: relative;
    &::before{
      position: absolute;
      content: '';
      width: 40px;
      height: 2px;
      left: 20px;
      bottom: 0;
      background: $primary-color;
      transform: scaleX(0);
      transform-origin: right;
      transition: transform .3s ease-in-out;
    }
    &:hover{
      &::before{
        transform: scaleX(1);
        transform-origin: left;
      }
    }
    @include tablet {
      padding: 5px;
    }
  }
  &:not(:last-child){
    &::before{
      position: absolute;
      content: "";
      height: 10px;
      width: 5px;
      background: transparent;
      border-left: 1px solid $primary-color;
      border-right: 1px solid $primary-color;
      right: -10px;
      top: 50%;
      transform: translateY(-50%);
      @include tablet {
        display: none
      }
    }
  }
}

.navbar-light .navbar-nav .nav-link {
  color: $text-color;
  &:hover{
    color: $primary-color;
  }
}

.select{
  color: $text-color;
}