.btn {
  font-size: 16px;
  font-family: $primary-font;
  text-transform: capitalize;
  padding: 15px 30px;
  font-weight: 700;
  border: 0;
  border-radius: 50px;
  position: relative;
  z-index: 1;
  transition: .2s ease;
  box-shadow: 0px 8px 22px 0px rgba(67, 66, 68, 0.22);

  &:focus {
    outline: 0;
    box-shadow: none !important;
  }

  &:active {
    box-shadow: none;
  }
}

.btn-primary {
  background-color: $primary-color;
  color: $white;

  &:active {
    background-color: darken($color: $primary-color, $amount: 10);
  }

  &:hover {
    background-color: darken($color: $primary-color, $amount: 10);
    color: $white;
  }
}
