/* 1.1 typography */
@import url('https://fonts.googleapis.com/css?family=K2D:300,400,500|Muli:300,400,700');

body {
  line-height: 1.5;
  font-family: $primary-font;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  font-size: 16px;
  color: $text-color;
}

p, .paragraph {
  font-weight: 400;
  color: $text-color;
  font-size: 16px;
  line-height: 1.5;
  font-family: $primary-font;
}

h1,h2,h3,h4,h5,h6 {
  color: $text-color;
  font-family: $secondary-font;
  font-weight: 300;
  line-height: 1.3;
}

h1, .h1{
  font-size: 45px;
}

h2, .h2{
  font-size: 36px;
}

h3, .h3{
  font-size: 30px;
}

h4, .h4{
  font-size: 22px;
}

h5, .h5{
  font-size: 16px;
}

h6, .h6{
  font-size: 13px;
}
