.hero-section{
  overflow: hidden;
}
.hero-slider {
  .slick-list{
    overflow: visible;
  }
  .slick-dots {
    display: flex;
    justify-content: center;
    padding-left: 0;

    li {
      margin: 0 10px;

      &.slick-active {
        button {
          background: $primary-color;

          &::before {
            transform: scale(1);
          }
        }
      }
    }

    button {
      height: 6px;
      width: 6px;
      border-radius: 50%;
      background: $text-color-light;
      color: transparent;
      padding: 0;
      border: 0;
      position: relative;

      &::before {
        position: absolute;
        left: -5px;
        top: -5px;
        height: 16px;
        width: 16px;
        background: transparent;
        border: 1px solid $text-color-light;
        content: '';
        border-radius: 50%;
        transform: scale(0);
        transition: .2s ease;
      }
    }
  }
}

.banner-img{
  position: relative;
  width: 560px;
  height: 580px;
  margin: 0 auto;
  @include desktop-lg {
    width: 400px;
    height: 410px;
    img{
      max-width: 250px;
    }
  }
  @include desktop {
    width: 360px;
    height: 370px;
    img{
      max-width: 235px;
    }
  }
  @include tablet {
    height: 510px;
    width: 500px;
    img{
      max-width: 320px;
    }
  }
  @include mobile {
    height: 320px;
    width: 320px;
    img{
      max-width: 200px;
    }
  }
  @include mobile-xs {
    height: 280px;
    width: 280px;
    img{
      max-width: 180px;
    }
  }
  .fourth-layer{
    position: absolute;
    right: 0;
    bottom: 10px;
  }
  .third-layer{
    position: absolute;
    left: 10px;
    bottom: 0;
  }
  .second-layer{
    position: absolute;
    top: 10px;
    left: 0;
  }
  .first-layer{
    position: absolute;
    top: 0;
    right: 10px;
  }
}

.social-icons {
  li {
    i {
      font-size: 20px;
      color: $text-color-light;
      transition: .2s ease;

      &.facebook {
        &:hover {
          color: #4d6fa9;
        }
      }

      &.instagram {
        &:hover {
          color: #D52C83;
        }
      }

      &.twitter {
        &:hover {
          color: #1DA1F3;
        }
      }
    }
  }
}

/* display grid */
.grid {
  display: grid;
  grid-template-areas:
    'a a b'
    'c d b'
    'e d f'
    'e g g'
}

.grid-item{
  border-radius: 5px;
  overflow: hidden;
  cursor: pointer;
  img{
    transition: .5s ease;
  }
  &:hover{
    img{
      transform: scale(1.2);
    }
  }
}

.grid-item1 {
  grid-area: a;
}

.grid-item2 {
  grid-area: b;
}

.grid-item3 {
  grid-area: c;
}

.grid-item4 {
  grid-area: d;
}

.grid-item5 {
  grid-area: e;
}

.grid-item6 {
  grid-area: f;
}

.grid-item7 {
  grid-area: g;
}


.form-control {
  border: 0;
  border-radius: 0;
  border-bottom: 1px solid $text-color;
  height: 45px;
  padding-left: 50px;
  &:focus{
    box-shadow: none;
    border-color: $primary-color;
  }

  @include mobile {
    padding-left: 20px;
  }
}

.input-wrapper {
  position: relative;

  .submit-btn {
    position: absolute;
    top: 10px;
    right: 50px;
    border: 0;
    background: transparent;

    @include mobile {
      right: 20px;
    }
  }
}

.about-shape {
  position: relative;

  .shape-1 {
    position: absolute;
    left: 50%;
    top: -50px;
    z-index: -1;
  }

  .shape-2 {
    position: absolute;
    right: 0;
    top: 0px;
    z-index: -1;
  }

  .shape-3 {
    position: absolute;
    right: 0;
    bottom: 50px;
    z-index: -1;
  }
}

.service-shape {
  position: relative;

  .shape-1 {
    position: absolute;
    left: 0;
    top: 50px;
    z-index: -1;
  }

  .shape-2 {
    position: absolute;
    left: 45%;
    top: 400px;
    z-index: -1;
  }

  .shape-3 {
    position: absolute;
    right: 50px;
    top: 600px;
    z-index: -1;
  }

  .shape-4 {
    position: absolute;
    left: 50%;
    top: 60%;
    z-index: -1;
  }

  .shape-5 {
    position: absolute;
    left: 0px;
    bottom: 300px;
    z-index: -1;
  }

  .shape-6 {
    position: absolute;
    right: 50%;
    bottom: 0px;
    z-index: -1;
  }
}

.footer-shape {
  position: relative;

  .shape-1 {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  .shape-2 {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
  }

  .shape-3 {
    position: absolute;
    left: 100px;
    bottom: 80px;
    z-index: -1;
  }

  .shape-4 {
    position: absolute;
    right: 100px;
    bottom: 80px;
    z-index: -1;
  }
}